@font-face {
    font-family: 'Fortnite';
    src: url('../../fonts/fortnite.ttf') format('truetype');
    font-weight: bold;
    font-style: normal;
}

.home-container {
    width: 100%;
    height: auto;
    background: #A3BBFB;
    overflow: hidden;
}

.home-title {
    padding-top: 20px;
    padding-bottom: 20px;
    font-family: 'Fortnite', serif;
    font-size: 50px;
    color: white;
    width: 100%;
    text-align: center;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.avatar-grid-container {
    width: 100%;
    height: auto;
    position: relative;
    display: grid;
    grid-template-areas: " hello career hobbies family";
    padding-bottom: 30px;/* Define grid areas */
}

.web-title {
    grid-area: title;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.image-container {
    opacity: 0.85;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-top: 100px;
    width: 100%;
    height: auto;
}

.image-container:hover .me-select {
    transform: scale(1.1); /* Increase image size by 10% */
    transition: transform 0.3s ease; /* Smooth transition */
}

.image-container:hover .image-label {
    transform: scale(1.1); /* Increase label size by 10% */
    transition: transform 0.3s ease; /* Smooth transition */
}

.image-container:hover {
    opacity: 1.0;
    transition: transform 0.3s ease;
    cursor: pointer;
}

.me-select{
    width: 100%;
    max-width: 400px;
}

.image-label {
    font-family: 'Fortnite', serif;
    font-size: 34px;
    color: white;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.image-container:nth-child(1) { /* career */
    grid-area: hello;
}

.image-container:nth-child(2) { /* hello */
    grid-area: career;
}

.image-container:nth-child(3) { /* hobbies */
    grid-area: hobbies;
}

.image-container:nth-child(4) { /* family */
    grid-area: family;
}

@media screen and (max-width: 750px)  {
    .title-container {
        padding-top: 10px;
    }

    .home-container {
        height: 100%;
        overflow: scroll;
    }

    .avatar-grid-container {
        display: block;
        height: 100%;
    }

    .image-container{
        margin-top: 0;
        margin-bottom: 20px;
        width: 100%;
        align-items: start;
        text-align: left;
        display: inline-block;
    }

    .me-select {
        width: 40%;
    }

    .image-label {
        height: 100%;
        margin-left: 10px;
        float: left;
        font-size: 40px;
        text-align: center;
        padding-top: 10%;
        width: 30%;
    }
}
